.pop-up {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 3;
  background-color: rgba(0, 0, 0, 0.5);
  min-height: 100vh;
  min-width: 100vw;
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.container {
  background-color: white;
  min-height: 50vh;
  min-width: 50vw;
  padding: 1rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.5rem;
  border-radius: 0.2rem;
}

.container button.close {
  align-self: flex-end;
  background-color: transparent;
  color: black;
  box-shadow: none;
}

.container button.close:hover {
  background-color: transparent;
}
